.App {
  text-align: center;
  /* background-color: #c5f0df; */
  background: linear-gradient(120deg, #c5f0df, #e2f073, #b1f0bc);
  background-size: 10000% 10000%;
        
  -webkit-animation: AnimationName 10s ease infinite;
  animation: AnimationName 10s ease infinite;
  
}

.AppUpper{

  background:-webkit-gradient( linear, left top, left bottom, from(rgba(71, 71, 71, 0.5)), to(rgba(71, 71, 71, 0.5)) ), url(/static/media/weatheringWithYouCrop1.fcdf95aa.png), fixed;

  background:linear-gradient( rgba(71, 71, 71, 0.5), rgba(71, 71, 71, 0.5) ), url(/static/media/weatheringWithYouCrop1.fcdf95aa.png), fixed;


}

@-webkit-keyframes AnimationName {
  0%{background-position:0% 57%}
  50%{background-position:100% 44%}
  100%{background-position:0% 57%}
}
@keyframes AnimationName {
  0%{background-position:0% 57%}
  50%{background-position:100% 44%}
  100%{background-position:0% 57%}
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

